.post__title {
  margin-top: 45px;
  margin-bottom: 45px;
  padding: 0 33px;
}

.post__container {
  background-color: #f2f2f2;
  padding-bottom: 35px;
}

.post__photo-container {
  display: block;
  width: 100%;
  margin-bottom: 35px;
}

.post__control-container {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.post__icon--active path:nth-child(2),
.post__icon--active rect,
.post__control-item--contrast > .post__icon--active path {
  fill: #d22856;
  opacity: 1;
}

.post__icon:hover * {
  opacity: 0.7;
}

.post__control-item {
  position: relative;
  display: flex;
  align-items: center;
  margin-right: 37px;
  cursor: pointer;
}

.post__control-item:nth-child(3) {
  margin-right: 15px;
}

.post__wrapper {
  padding-top: 20px;
}

.post__line {
  height: 1px;
  background: #dddfe0;
  opacity: 0.3;
}

.post__btn-container {
  width: 100%;
  padding-top: 107px;
}

@media (min-width: @tablet-width) {
  .post__line {
    display: none;
  }

  .post__title {
    padding: 0;
    margin-top: 45px;
    margin-bottom: 69px;
  }

  .post__icon path:not(:first-child),
  .post__icon rect,
  .post__icon--contrast path {
    fill: #d22856;
    opacity: 1;
  }

  .post__wrapper {
    display: flex;
    padding-top: 55px;
    padding-bottom: 50px;
    justify-content: space-between;
  }

  .post__photo-container {
    margin-bottom: 0;
    width: 300px;
  }

  .post__control-container {
    width: 275px;
    padding-top: 10px;
    flex-direction: column;
  }

  .post__btn-container {
    padding: 0;
    margin-top: auto;
  }

  .post__control-item {
    min-height: auto;
    padding-bottom: 24px;
    margin-bottom: 24px;
    border-bottom: 1px solid #dddfe0;
  }

  .post__icon {
    position: relative;
    top: 3px;
    width: 22px;
    height: 22px;
    padding-left: 4px;
  }

  .post__icon--contrast {
    width: 17px;
    height: 17px;
    padding-left: 8px;
    padding-top: 2px;
  }

  .post__container {
    padding-bottom: 0;
  }
}

@media (min-width: @desktop-width) {
  .post__control-container {
    width: 410px;
    padding-top: 0;
  }

  .post__btn-container {
    display: flex;
    justify-content: space-between;
    margin-top: 132px;
  }

  .post__icon {
    width: 46px;
    height: 48px;
    padding: 0;
  }

  .post__icon--contrast {
    width: 33px;
    height: 33px;
    padding-left: 5px;
  }

  .post__title {
    margin-top: 11px;
    margin-bottom: 65px;
  }

  .post__container {
    max-width: 940px;
    margin: 0 auto;
  }

  .post__wrapper {
    box-sizing: border-box;
    padding-bottom: 45px;
    padding-top: 53px;
    padding-left: 80px;
    padding-right: 60px;
  }

  .post__control-item {
    padding-bottom: 13px;
    margin-bottom: 15px;
  }

  .range-control--crop .range-control__bar {
    top: 4px;
  }

  .range-control--contrast .range-control__bar {
    top: -6px;
  }
}
