.range-control {
  display: none;
  position: absolute;
}

.range-control--crop {
  position: absolute;
  display: block;
  top: 82px;
  left: -14px;
}

.range-control__bar {
  position: relative;
  width: 240px;
  height: 2px;
  background-color: #283645;

  &::after {
    content: "";
    position: absolute;
    top: -7px;
    left: 193px;
    display: block;
    box-sizing: border-box;
    width: 15px;
    height: 15px;
    border-radius: 50%;
    background-color: #f2f2f2;
    border: 2px solid #d22856;
    cursor: pointer;
  }

  &:active::after {
    background-color: #d22856;
  }
}

.range-control__toggle:hover::after {
  background-color: #d22856;
}

@media (min-width: @tablet-width) {
  .range-control__bar {
    width: 202px;
  }

  .range-control {
    top: 10px;
    left: 50px;
  }

  .range-control--crop {
    top: inherit;
  }

  .range-control--crop .range-control__bar::after {
    left: 150px;
  }

  .range-control--fill .range-control__bar::after {
    left: 30px;
  }

  .range-control--contrast .range-control__bar::after {
    left: 100px;
  }

  .range-control--fill {
    display: block;
  }

  .range-control--contrast {
    display: block;
  }
}

@media (min-width: @desktop-width) {
  .range-control__bar {
    width: 288px;
  }

  .range-control {
    left: 93px;
    top: 20px;
  }

  .range-control--crop .range-control__bar::after {
    left: 70px;
  }

  .range-control--fill .range-control__bar::after {
    left: 245px;
  }

  .range-control--contrast .range-control__bar::after {
    left: 160px;
  }
}
