@font-face {
  font-family: "OpenSans";
  src: url("../../fonts/opensans.woff2") format("woff2"), url("../../fonts/opensans.woff") format("woff");
  font-style: normal;
  font-weight: normal;
}
@font-face {
  font-family: "OpenSans";
  src: url("../../fonts/opensansbold.woff2") format("woff2"), url("../../fonts/opensansbold.woff") format("woff");
  font-style: normal;
  font-weight: bold;
}
@font-face {
  font-family: "OpenSans";
  src: url("../../fonts/opensanslight.woff2") format("woff2"), url("../../fonts/opensanslight.woff") format("woff");
  font-style: normal;
  font-weight: 300;
}
body {
  margin: 0;
  min-width: 320px;
  font-family: "OpenSans", "Arial", sans-serif;
  color: #283645;
  font-size: 14px;
  line-height: 1.7em;
}
.link-normalize {
  text-decoration: none;
}
.list-item-reset {
  padding: 0;
  margin: 0;
}
.list-normalize {
  padding: 0;
  margin: 0;
  list-style: none;
}
.wrapper {
  position: relative;
  width: 280px;
  margin: 0 auto;
}
@media (min-width: 660px) {
  .wrapper {
    width: 620px;
  }
}
@media (min-width: 960px) {
  .wrapper {
    width: 940px;
  }
  .wrapper--global-competision {
    width: 940px;
    margin: 0 auto;
  }
  .competision-form__personal--name {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  .competision-form__label {
    display: flex;
    align-items: center;
  }
}
.page-header .wrapper {
  width: 100%;
}
.page-header__pic {
  display: block;
  padding: 23px 20px;
}
.page-header__pic:hover path {
  fill: #d02856;
}
.page-header__pic:active path {
  fill: #ffffff;
  opacity: 0.3;
}
.page-header__menu-toggle {
  position: absolute;
  top: 20px;
  right: 20px;
  padding: 0;
  background-color: transparent;
  border: 0;
  font-size: 0;
  cursor: pointer;
}
.page-header__menu-toggle:hover path {
  fill: #d02856;
}
.page-header__menu-toggle:active path {
  fill: #d02856;
  opacity: 0.3;
}
.page-header__menu-container {
  position: relative;
  z-index: 1;
  margin-bottom: -66px;
  background-color: rgba(0, 0, 0, 0.3);
}
.page-header__logo {
  display: inline-block;
  vertical-align: middle;
  padding-left: 20px;
  align-items: center;
}
.page-header__logo svg {
  padding-top: 19px;
  padding-bottom: 19px;
}
@media (min-width: 660px) {
  .page-header__menu-container {
    margin-bottom: -110px;
  }
  .page-header__logo svg {
    padding-bottom: 35px;
  }
  .page-header__menu-toggle {
    top: 45px;
  }
  .page-header__logo {
    display: inline-flex;
    min-height: 110px;
  }
}
@media (min-width: 960px) {
  .page-header__menu-container .wrapper {
    display: flex;
  }
  .page-header .wrapper {
    width: 940px;
    margin: 0 auto;
  }
  .page-header__logo {
    padding-left: 0;
  }
}
.site-menu__list {
  padding: 0;
  margin: 0;
  list-style: none;
}
.site-menu__link {
  text-decoration: none;
  display: flex;
  align-items: center;
  position: relative;
  padding: 22px 0;
  text-transform: uppercase;
  color: #ffffff;
  font-size: 18px;
  line-height: 18px;
  font-weight: 700;
}
.site-menu__link:hover {
  color: #d02856;
}
.site-menu__link:active {
  color: #ffffff;
  opacity: 0.3;
}
@media (max-width: 959px) {
  .site-menu--closed .site-menu__item {
    display: none;
  }
  .site-menu--closed .page-header__cross-btn {
    display: none;
  }
  .site-menu--open .page-header__burger-btn {
    display: none;
  }
  .site-menu--closed svg {
    width: 49px;
    height: 25px;
  }
  .site-menu--open svg {
    width: 24px;
    height: 23px;
  }
  .page-header__menu-container--open {
    background-color: #283645;
    margin-bottom: 0;
  }
  .site-menu--open .site-menu__item {
    display: flex;
    justify-content: center;
    align-items: center;
    border-top: 1px solid #485461;
    border-bottom: 1px solid #000000;
  }
  .site-menu.main-nav--nojs {
    display: block;
    width: 100%;
    justify-content: center;
    align-items: center;
    border-top: 1px solid #485461;
    border-bottom: 1px solid #000000;
  }
  .site-menu.main-nav--nojs:last-child {
    border-bottom: none;
  }
  .main-nav--nojs .page-header__menu-toggle {
    display: none;
  }
}
@media (min-width: 960px) {
  .site-menu--closed .site-menu__item {
    display: flex;
    justify-content: center;
    align-items: center;
    border: none;
  }
  .site-menu__link {
    padding-top: 46px;
    padding-bottom: 46px;
    font-weight: 400;
    text-transform: none;
  }
  .page-header__menu-container .wrapper {
    justify-content: space-between;
  }
  .page-header__menu-toggle {
    display: none;
  }
  .site-menu {
    width: 620px;
    display: flex;
  }
  .site-menu__list {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    justify-content: space-between;
  }
  .site-menu__link--active {
    position: relative;
  }
  .site-menu__link--active::before {
    content: "";
    position: absolute;
    top: 75px;
    display: block;
    width: 100%;
    height: 2px;
    background-color: #ffffff;
  }
}
.button {
  text-decoration: none;
  display: inline-block;
  vertical-align: middle;
  padding: 15px 5px;
  border: 3px solid #ffffff;
  border-radius: 30px;
  text-align: center;
  color: #ffffff;
}
.button--add {
  width: 100%;
  box-sizing: border-box;
  padding: 16px 0;
  margin-bottom: 10px;
  border-color: #d22856;
  background-color: #f2f2f2;
  font-size: 18px;
  font-weight: 700;
  color: #d22856;
  cursor: pointer;
}
.button--add:hover {
  background-color: #d22856;
  color: #ffffff;
}
.button--add:active {
  color: #e08195;
}
.button--cancel {
  width: 100%;
  margin-bottom: 10px;
  box-sizing: border-box;
  border-color: #dddfe0;
  background-color: #f2f2f2;
  font-weight: 700;
  text-transform: uppercase;
  color: #dddfe0;
  cursor: pointer;
}
.button--cancel:hover {
  border-color: #b5b9be;
}
.button--cancel:active {
  background-color: #b5b9be;
  color: #cdd0d3;
}
@media (min-width: 660px) {
  .button--add,
  .button--cancel {
    width: 261px;
    padding: 14px;
    margin-bottom: 25px;
    font-family: inherit;
    text-transform: none;
    font-size: 18px;
    font-weight: 700;
  }
  .button--cancel {
    margin-bottom: 0;
  }
}
@media (min-width: 960px) {
  .button--add,
  .button--cancel {
    font-family: inherit;
    width: 180px;
    margin-bottom: 0;
    font-weight: 700;
  }
}
